import React, { Component } from 'react';
import { Modal, Button, Container, Row, Col, InputGroup, FormControl } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import axios from 'axios';
import VideoPlayer from '../components/VideoPlayer';
import Comments from '../components/Comments';
import Reactions from '../components/Reactions';
import VideoBG from '../components/VideoBG';
import './Main.css';
import * as utils from '../utils/utils';
const PARAMS = 'like,love,wow,haha,sad,angry'.split(',').map(type => `reactions.type(${type.toUpperCase()}).limit(0).summary(total_count).as(${type})`).join(',');
class Main extends Component {
    constructor(props) {
        super(props);
        this.handleShow = this.handleShow.bind(this);
        this.handleClose = this.handleClose.bind(this);
        this.tokenChange = this.tokenChange.bind(this);
        this.postIdChange = this.postIdChange.bind(this);
        this.after = '';
        this.state = {
            status: false,
            modalShow: false,
            token: '',
            postId: '',
            backgroundUrl: '',
            fontSize: '14',
            url: '',
            playing: true,
            controls: false,
            volume: 0.8,
            muted: false,
            loop: false,
            timeout: 3,
            like: '',
            love: '',
            haha: '',
            sad: '',
            wow: '',
            angry: '',
            videoBG: '',
            buttonImg: 'https://raw.githubusercontent.com/j2teamlbh/J2TeamLive/master/logo-j2team-w.png',
            comments: []
        };
    }

    handleClose() {
        this.setState({ modalShow: false });
    }

    handleShow() {
        this.setState({ modalShow: true });
    }
    getReactions() {
        axios.get(`https://graph.facebook.com/${this.state.postId}?fields=${PARAMS}&access_token=${this.state.token}`)
            .then((post) => {
                this.setState({
                    like: post.data.like.summary.total_count,
                    love: post.data.love.summary.total_count,
                    haha: post.data.haha.summary.total_count,
                    sad: post.data.sad.summary.total_count,
                    wow: post.data.wow.summary.total_count,
                    angry: post.data.angry.summary.total_count
                });
            });
    }
    getComments() {
        axios.get(`https://graph.facebook.com/${this.state.postId}/comments?fields=message,from{name,picture}&limit=50&access_token=${this.state.token}&after=${this.after}`)
            .then(res => {
                if (res.data.data.length > 0) {
                    this.after = res.data.paging.cursors.after;
                    this.setState({ comments: [...res.data.data, ...this.state.comments] });
                }
            })
            .catch(err => {
                console.log(err);
            })
    }
    handleClick = () => {
        this.setState(state => {
            if (state.status) {
                clearInterval(this.timer);
            } else {
                this.timer = setInterval(() => {
                    this.getReactions();
                    this.getComments();
                }, state.timeout < 1 ? 3000 : state.timeout * 1000);
            }
            return { status: !state.status };
        });
    };

    componentWillUnmount() {
        clearInterval(this.timer);
    }
    tokenChange = (e) => {
        this.setState({
            token: e.target.value
        });
    }
    bgChange = (e) => {
        this.setState({
            backgroundUrl: e.target.value
        });
    }
    fontSizeChange = (e) => {
        this.setState({
            fontSize: parseFloat(e.target.value)
        });
    }

    urlChange = (e) => {
        this.setState({
            url: e.target.value
        });
    }
    videoBGChange = (e) => {
        this.setState({
            videoBG: e.target.value
        });
    }
    postIdChange = (e) => {
        this.setState({
            postId: e.target.value
        });
    }

    timeoutChange = (e) => {
        this.setState({
            timeout: e.target.value
        });
    }
    savefbdata = (val, val2) => {
        this.setState({
            token: val,
            postId: val2
        }, () => { this.handleClick() })
    }
    load = url => {
        this.setState({
            url,
            played: 0,
            loaded: 0,
            pip: false
        })
    }
    toggleControls = () => {
        const url = this.state.url
        this.setState({
            controls: !this.state.controls,
            url: null
        }, () => this.load(url))
    }
    toggleLoop = () => {
        this.setState({ loop: !this.state.loop })
    }
    toggleMuted = () => {
        this.setState({ muted: !this.state.muted })
    }
    setVolume = e => {
        this.setState({ volume: parseFloat(e.target.value) })
    }
    
    handleChangeFile = (file) => {
        this.setState({
            url: URL.createObjectURL(file)
        })
    }
    handleChangeFile2 = (file) => {
        this.setState({
            videoBG: URL.createObjectURL(file)
        })
    }
    handleChangeImage = (file) => {
        this.setState({
            backgroundUrl: URL.createObjectURL(file)
        })
    } 
    playPause = () => {
        this.setState({ playing: !this.state.playing })
    }
    buttonImgChange = (e) => {
        this.setState({
            buttonImg: e.target.value
        });
    }
    handleBtnImage = (file) => {
        this.setState({
            buttonImg: URL.createObjectURL(file)
        })
    }
    render() {
        const { like, love, haha, sad, wow, angry, token,buttonImg, postId, status, videoBG, modalShow, backgroundUrl, playing, timeout, url, fontSize, controls, volume, muted, loop } = this.state;
        const customFont = { fontSize: fontSize };
        return (
            <div className="live-main" style={{ backgroundImage: `url(${backgroundUrl})` }}>
                <div className="live-controls">
                    <Button variant="primary" className="btn-control" onClick={this.handleShow} style={{ backgroundImage: `url(${buttonImg})` }}></Button>
                </div>
                <Modal show={modalShow} size="lg" onHide={this.handleClose}>
                    <Modal.Header closeButton>
                        <Modal.Title>Controls</Modal.Title>
                        <Link to="/dualposts" className="ml-2 btn btn-danger">Dual Posts</Link>
                    </Modal.Header>
                    <Modal.Body>
                        <Container>
                            <h5>Facebook Connect</h5>
                            <Row>
                                <Col md={12} className="mb-3">
                                    <InputGroup className="mb-3">
                                        <InputGroup.Prepend>
                                            <InputGroup.Text id="basic-addon1">Access Token</InputGroup.Text>
                                        </InputGroup.Prepend>
                                        <FormControl
                                            placeholder="EAAAAAYsX7TsBAGNuxoKZC6V1DVi..."
                                            aria-label="Access Token"
                                            value={token}
                                            onChange={this.tokenChange}
                                        />
                                    </InputGroup>
                                    <InputGroup className="mb-3">
                                        <InputGroup.Prepend>
                                            <InputGroup.Text>Post ID</InputGroup.Text>
                                        </InputGroup.Prepend>
                                        <FormControl
                                            placeholder="1010500792634..."
                                            aria-label="Post ID"
                                            value={postId}
                                            onChange={this.postIdChange}
                                        />
                                    </InputGroup>
                                    <InputGroup>
                                        <InputGroup.Prepend>
                                            <InputGroup.Text>Request Time (Default: 3s)</InputGroup.Text>
                                        </InputGroup.Prepend>
                                        <FormControl
                                            type="number"
                                            value={timeout}
                                            onChange={this.timeoutChange}
                                            placeholder="Set time each get comments and reactions (Default: 3s)"
                                        />
                                        <InputGroup.Append>
                                            <Button variant="primary" className="btn-load" onClick={() => this.savefbdata(token, postId)}>
                                                {status ? 'Pause' : 'Load'}
                                            </Button>
                                        </InputGroup.Append>
                                    </InputGroup>
                                </Col>
                            </Row>
                            <h5>Live Customize</h5>
                            <Row className="mb-3">
                            <Col md={12}>
                                    <InputGroup>
                                        <InputGroup.Prepend>
                                            <InputGroup.Text id="basic-addon1">Button Image</InputGroup.Text>
                                        </InputGroup.Prepend>
                                        <FormControl
                                            placeholder="Ex: https://i.imgur.com/G2....jpg"
                                            aria-label="Background"
                                            value={buttonImg}
                                            onChange={this.buttonImgChange}
                                        />
                                        <InputGroup.Append>
                                            <Button variant="primary" className="btn-load" onClick={() => utils.chooseButtonBG()}>
                                                Choose File
                                            </Button>
                                            <FormControl
                                                type="file"
                                                id="bgButton"
                                                aria-label="Image File"
                                                accept="image/png, image/jpeg"
                                                onChange={e =>
                                                    this.handleBtnImage(e.target.files[0])}
                                                hidden
                                            />
                                        </InputGroup.Append>
                                    </InputGroup>
                                </Col>
                                <Col md={6}>
                                    <InputGroup>
                                        <InputGroup.Prepend>
                                            <InputGroup.Text id="basic-addon1">Background</InputGroup.Text>
                                        </InputGroup.Prepend>
                                        <FormControl
                                            placeholder="Ex: https://i.imgur.com/G2....jpg"
                                            aria-label="Background"
                                            value={backgroundUrl}
                                            onChange={this.bgChange}
                                        />
                                        <InputGroup.Append>
                                            <Button variant="primary" className="btn-load" onClick={() => utils.chooseBG()}>
                                                Choose File
                                            </Button>
                                            <FormControl
                                                type="file"
                                                id="bgFile"
                                                aria-label="Image File"
                                                accept="image/png, image/jpeg"
                                                onChange={e =>
                                                    this.handleChangeImage(e.target.files[0])}
                                                hidden
                                            />
                                        </InputGroup.Append>
                                    </InputGroup>
                                </Col>
                                <Col md={6} className="mb-3">
                                    <InputGroup>
                                        <InputGroup.Prepend>
                                            <InputGroup.Text id="basic-addon1">Font Size ({fontSize}px)</InputGroup.Text>
                                        </InputGroup.Prepend>
                                        <FormControl
                                            className="slider"
                                            type="range"
                                            max={40}
                                            aria-label="Font Size"
                                            value={fontSize}
                                            onChange={this.fontSizeChange}
                                        />
                                    </InputGroup>
                                </Col>
                                <Col md={12}>
                                    <InputGroup className="mb-3">
                                        <InputGroup.Prepend>
                                            <InputGroup.Text id="basic-addon3">
                                                Video Background(.mp4)
                                            </InputGroup.Text>
                                        </InputGroup.Prepend>
                                        <FormControl
                                            placeholder="Ex: https:https://sample-videos.com/..._bunny_720p_10mb.mp4"
                                            aria-label="Background"
                                            value={videoBG}
                                            onChange={this.videoBGChange}
                                        />
                                        <InputGroup.Append>
                                            <Button variant="primary" className="btn-load" onClick={() => utils.chooseFile2()}>
                                                Choose File
                                            </Button>
                                            <FormControl
                                                type="file"
                                                id="bgVideoFile"
                                                aria-label="Video/Audio File"
                                                onChange={e =>
                                                    this.handleChangeFile2(e.target.files[0])}
                                                hidden
                                            />
                                        </InputGroup.Append>
                                    </InputGroup>
                                </Col>
                                <Col md={12}>
                                    <InputGroup>
                                        <InputGroup.Prepend>
                                            <InputGroup.Text id="basic-addon1">Video/Audio Link</InputGroup.Text>
                                        </InputGroup.Prepend>
                                        <FormControl
                                            placeholder="Ex: https://youtube.com/..."
                                            aria-label="Video/Audio Link"
                                            value={url}
                                            onChange={this.urlChange}
                                        />
                                        <InputGroup.Append>
                                            <Button variant="primary" className="btn-load" onClick={() => utils.chooseFile()}>
                                                Choose File
                                            </Button>
                                            <FormControl
                                                type="file"
                                                id="chooseFile"
                                                aria-label="Video/Audio File"
                                                onChange={e =>
                                                    this.handleChangeFile(e.target.files[0])}
                                                hidden
                                            />
                                        </InputGroup.Append>
                                    </InputGroup>
                                </Col>
                            </Row>
                            <Row>
                                <Col md={12}>
                                    <InputGroup.Prepend>
                                        <Button variant="primary" className="btn-load" onClick={this.playPause}>
                                            {playing ? 'Pause' : 'Play'}
                                        </Button>
                                        <InputGroup.Checkbox checked={controls} onChange={this.toggleControls} />
                                        <FormControl value={'Controls'} disabled className="mr-2" />
                                        <InputGroup.Checkbox checked={muted} onChange={this.toggleMuted} />
                                        <FormControl value={'Mute'} disabled className="mr-2" />
                                        <InputGroup.Checkbox checked={loop} onChange={this.toggleLoop} />
                                        <FormControl value={'Loop'} disabled className="mr-2" />
                                        <InputGroup.Text id="basic-addon1">Volume</InputGroup.Text>
                                        <FormControl
                                            className="slider"
                                            type="range"
                                            aria-label="Volume"
                                            min={0} max={1} step='any'
                                            value={volume}
                                            onChange={this.setVolume}
                                        />
                                    </InputGroup.Prepend>
                                </Col>
                            </Row>
                        </Container>
                    </Modal.Body>
                    <Modal.Footer>
                        <span>© 2019 made with ♡ by <a href="https://fb.com/5515886">Lê Hậu</a></span>
                    </Modal.Footer>
                </Modal>
                <div className="videoBG">
                    <div className="overlay"></div>
                    <VideoBG videoBG={videoBG} />
                </div>
                <div className="main-area">
                    <VideoPlayer
                        url={url}
                        playing={playing}
                        controls={controls}
                        volume={volume}
                        muted={muted}
                        loop={loop} />
                    <Reactions like={like} love={love} haha={haha} sad={sad} wow={wow} angry={angry} />
                    <div className="comments">
                        {this.state.comments.map((comment, key) => <Comments customFont={customFont} key={key} imageSrc={comment.from.picture} name={comment.from.name} message={comment.message} />)}
                    </div>
                </div>
            </div>
        );
    }
}

export default Main;