import React, { Component } from 'react';
import ReactPlayer from 'react-player';
class VideoPlayer extends Component {
    render() {
        const { url, playing, controls, volume, muted, loop } = this.props
        return (
            <div className="videoplayer" style={this.props.videoplayer}>
                <ReactPlayer
                    className='react-player'
                    width="100%"
                    height="100%"
                    url={url}
                    playing={playing}
                    controls={controls}
                    loop={loop}
                    volume={volume}
                    muted={muted}
                />
            </div>
        );
    }
}

export default VideoPlayer;